




































































.error {
    border-color: red;
}

.form-group {
    text-align: center;
    min-width: 70%;
}

ul {
    position: absolute;
    list-style: none;
    max-width: 40rem;
    height: 20rem;
    overflow: auto;

    li {
        &:hover {
            cursor: pointer;
            font-size: 1.15rem;
            z-index: 2;
            color: #fff;
            background-color: #007bff;
            border-color: #007bff;
        }
    }
}
